.catalog {
  grid-area: catalog;
  font-family: "Roboto", "Helvetica", sans-serif;
  box-sizing: border-box;

  display: grid;
  grid-template-columns: repeat($col-count, 200px);

  padding: 50px 70px;
  row-gap: 48px;
  column-gap: 48px;
  justify-content: center;

  @media (max-width: 1023px) {
    $col-count: 3;
    grid-template-columns: repeat($col-count, 200px);
  }

  @media (max-width: 767px) {
    $col-count: 2;
    grid-template-columns: repeat($col-count, 200px);
  }

  @media (max-width: 487px) {
    $col-count: 1;
    grid-template-columns: repeat($col-count, 200px);
  }

  &__card {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 32px 16px 16px;

    color: $main-color;
    border: 1px solid #f3f3f3;
    border-radius: 5px;

    &-image {
      display: block;
      width: 160px;
      height: 134px;
      margin: 0 3px;
    }

    &-name {
      margin: 40px 0 0;

      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
    }

    &-code {
      font-size: 10px;
      margin: 4px 0 0;
      line-height: 14px;

      color: $secondary-color;
    }

    &-rating {
      display: flex;
      justify-content: space-between;
      align-items: center;

      margin-top: 16px;
    }

    &-reviews {
      font-size: 10px;
      margin: 0;
    }

    &-price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 60px;

      line-height: 18px;
      margin-top: 24px;
    }

    &-price-text {
      font-size: 12px;
      color: $secondary-color;
      margin: 0;
    }

    &-price-amount {
      font-size: 16px;
      font-weight: 700;
      margin: 0;
    }

    &-buy-button {
      display: block;
      text-align: center;
      line-height: 40px;
      width: 166px;
      height: 40px;
      margin-top: 16px;

      text-decoration: none;
      color: $button-secondary-color;
      font-size: 14px;
      font-weight: 700;
      border: 1px solid $button-main-color;
      border-radius: 5px;
      background-color: $button-main-color;

      &:hover {
        color: $button-main-color;
        background-color: $button-secondary-color;
      }
    }
  }
}

